import React, { PureComponent } from 'react';
import { connect } from 'react-redux';

import { STORE_KEY } from '../../constants';
import { selectChannel } from '../../actions';

class ChannelsList extends PureComponent {
  render() {
    const { visibleChannels, viewportHeight } = this.props;

    return (
      <div className="channelsList" style={{ height: viewportHeight }}>
        {visibleChannels.map(channel => (
          <div
            className="channelName"
            key={channel}
            onClick={() => {
              this.props.dispatch(selectChannel(channel));
            }}
          >
            {channel}
          </div>
        ))}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  visibleChannels: state.viewMode.visibleChannels,
  viewportHeight: state.presentation.viewportHeight,
});

export default connect(mapStateToProps, undefined, undefined, {
  storeKey: STORE_KEY,
})(ChannelsList);
